*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Merriweather", serif;
  font-weight: 400;
  font-style: normal;
  scroll-behavior: smooth;
  
}

.left-corner-button {
  position: fixed;
  top: 10px;
  left: 10px;
  background: none; 
  border: none; 
  color: #112A46; 
  font-size: 25px; 
  cursor: pointer; 
  padding: 0; 
  z-index: 1000; 
  text-decoration: none; 
}

.left-corner-button:hover {
  color: #C20944; 
  text-decoration: none; 
}

.home-page {
  background-image: url(/src/images/final-bg.jpg);
  background-size: cover;
  background-position: center;
  height: 100vh;
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.navbar {
  padding: 15px;
  display: flex;
  justify-content: flex-end; /* Align to the right */
  background-color: transparent; /* Use transparent if you don't need a background */
  height: 55px;
  margin: 0;
  width: 100%;
  position: absolute; /* Ensure it is positioned at the top */
  top: 0; /* Stick to the top */
  right: 0; /* Stick to the right */
}

.navbar-toggle {
  display: none; /* Hide by default */
  flex-direction: column;
  cursor: pointer;
  position: absolute; /* Position it absolutely within the navbar */
  top: 15px; /* Adjust top position to match navbar padding */
  right: 15px; /* Align to the right with padding */
  z-index: 1000; /* Ensure it appears above other content */
}

.navbar-toggle span {
  background: #112A46;
  height: 3px;
  width: 25px;
  margin: 4px 0;
}

.nav-list {
  display: flex;
  list-style-type: none;
  transition: all 0.3s ease;
}

.nav-list.active {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 55px;
  left: 0;
  right: 0;
  background: rgba(255, 255, 255, 0.9); /* White with 90% opacity */
  border-bottom: 1px solid #ddd;
  padding: 10px 0;
}

.nav-list.active .nav-item a {
  font-weight: bold;
}

.nav-item {
  margin-left: 30px;
  font-size: 20px;
}

.nav-item a {
  color: #112A46;
  text-decoration: none;
}

.nav-item a:hover {
  color: #C20944;
  text-decoration: none;
}

.myname {
  text-align: center;
  color: #112A46;
}

.myname h1 {
  margin: 0;
  padding: 0;
  font-size: 3rem;
}

.myname h3 {
  margin-top: 20px;
  padding: 0;
  font-size: 2rem;
}

.socialmedia {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

.media-list {
  display: flex;
  list-style-type: none;
  color: #112A46;
  font-size: 25px;
  gap: 10px;
}

.item a {
  color: #112A46;
  text-decoration: none;
}

.item a:hover {
  color: #C20944;
  text-decoration: none;
}

.me-button {
  display: flex;
  justify-content: center;
  margin-top: 15px;
  margin-bottom: 15px;
}

.me {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #5F1818;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
}

.me a {
  color: white;
  text-decoration: none;
}

.me a:active {
  color: #F1ECA0;
  text-decoration: underline;
}

.me a:hover {
  color: white;
  text-decoration: none;
}

.me a:focus {
  color: white;
  text-decoration: none;
}

/* Responsive styles */

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .navbar {
    justify-content: space-between; /* Space between logo and toggle */
  }

  .navbar-toggle {
    display: flex; /* Show toggle button */
  }

  .nav-list {
    display: none; /* Hide by default */
    flex-direction: column;
    width: 100%;
    text-align: center;
  }

  .nav-list.active {
    display: flex; /* Show when active */
  }

  .nav-item {
    margin: 10px 0;
  }

  .myname h1 {
    font-size: 1.7rem;
    padding-left: 10px;
    text-align: center;
  }

  .myname h3 {
    font-size: 1.1rem;
  }

  .me-button {
    width: 100%;
    text-align: center;
  }

  .me {
    width: auto;
    padding: 8px 16px; /* Adjust padding to make the button smaller */
    font-size: 14px; /* Adjust font-size to make the text smaller */
  }

  .socialmedia {
    margin-top: 20px;
    justify-content: center;
  }

  .media-list {
    gap: 5px;
  }

  
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
  .myname h1 {
    font-size: 2.5rem;
    padding-left: 20px;
    text-align: center;
  }

  .myname h3 {
    font-size: 1.75rem;
  }

  .me-button {
    width: auto;
    text-align: center;
  }

  .me {
    width: auto;
  }

  .media-list {
    gap: 8px;
  }
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
  .myname h1 {
    font-size: 3rem;
    padding-left: 20px;
    text-align: center;
  }

  .myname h3 {
    font-size: 2rem;
  }

  .me-button {
    width: auto;
    text-align: center;
  }

  .me {
    width: auto;
  }

  .media-list {
    gap: 10px;
  }
}

