.projectpage {
  background: rgb(206, 226, 255);
  background: linear-gradient(0deg, rgba(206, 226, 255, 1) 7%, rgba(249, 250, 187, 1) 60%);
  background-size: cover;
  background-position: center;
  height: 100vh;
  margin: 0;
  padding: 20px;
}

.pro-heading {
  text-align: center;
  font-size: 3rem;
  color: #161154;
  padding-top: 15px;
  margin-bottom: 1px;
}

.slider-container {
  width: 100%;
  padding: 20px;
  position: relative;
}

.slick-list {
  padding: 0 20px;
}


.project-slide {
  border-radius: 10px;
  padding: 5px;
  text-align: center;
  height: 400px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative; 
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: rgba(101, 161, 213, 0.2); 

}
.slick-slide>div{
  margin: 20px;
  
}

.image-container {
  position: relative;
}

.project-image {
  width: 100%;
  height: auto;
  max-height: 150px;
  object-fit: contain; 
  border-radius: 10px;
  margin-bottom: 20px;
  transition: opacity 0.3s ease;
  margin-top: 10px;
}

.image-container:hover .project-image {
  opacity: 0.2; 
}

.image-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.1); 
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  opacity: 0; 
  transition: opacity 0.3s ease; 
}

.image-container:hover .image-overlay {
  opacity: 1; 
}

.project-title {
  color: #161154;
  font-size: 1.5rem;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  z-index: 1;
  font-weight: bold;
}

.project-slide h3 {
  margin-bottom: 10px;
  font-size: 1.5rem;
  color: #333;
}

.project-slide p {
  font-size: 13px;
  color: #161154;
  text-align: justify;
  margin-bottom: 20px;
  flex: 1; 
  padding-left: 20px;
  padding-right: 20px;
}

.view-project-button-container {
  display: flex;
  justify-content: center; 
  position: absolute; 
  bottom: 20px; 
  left: 50%;
  transform: translateX(-50%); 
  width: 100%; 
